<template>
  <Container title="商品销量分析" :time="time" :nums="nums" detailPath="/goods?index=1">
    <div class="rank-title flex ac center">
      <img :src="require('@/assets/rank.png')" alt /> 商品销售榜单
    </div>
    <div class="rank-list">
      <div v-for="(item, index) in list" :key="item.goods_id" class="list-item flex ac js">
        <img class="rank-icon" v-if="index === 0" :src="require('@/assets/one.png')" alt />
        <img class="rank-icon" v-if="index === 1" :src="require('@/assets/two.png')" alt />
        <img class="rank-icon" v-if="index === 2" :src="require('@/assets/three.png')" alt />
        <CustomImg width="1.33rem" height="1.33rem" radius='0.13rem' fit='cover' lazy-load :src="item.goods_info.original_img"  />
        <div class="item goods-name">{{item.goods_info.goods_name}}</div>
      </div>
      <div v-if="!list.length" class="empty-text">暂无数据~</div>
    </div>
  </Container>
</template>

<script>
import { getGoodsAnalyze } from "@/api";
import Container from "./Container";

export default {
  components: { Container },
  data() {
    return {
      time: "", // 统计时间
      nums: [], // 数量
      list: [] // 榜单
    };
  },
  created() {
    this.getData();
  },
  methods: {
    // 商品销量数据
    getData() {
      getGoodsAnalyze().then(res => {
        if (res.success) {
          this.time = res.data.time;
          const nums = [];
          nums.push(
            { name: "昨日销量(件)", num: res.data.sale_num },
            { name: "昨日退款(件)", num: res.data.refund_num }
          );
          this.nums = nums;
          this.list = res.data.list.splice(0,3);
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.rank-title {
  width: 123px;
  height: 23px;
  background: rgba(201, 43, 42, 1);
  border-radius: 0px 50px 50px 0px;
  box-sizing: border-box;
  color: #fff;
  font-size: 14px;
  img {
    width: 15px;
    margin-right: 4px;
  }
}

.rank-list {
  padding: 15px 0;
  overflow: hidden;
}

.list-item{
  margin-bottom: 15px;
  &:last-child{
    margin-bottom: 0
  }
}
.rank-icon{
  width: 15px;
  margin: 0 10px;
}

.goods-name{
  padding: 0 10px;
  color: #252525;
  font-size: 14px;
  line-height: 20px;
}
</style>