<template>
  <div class="data-page">
    <Tabs :tabs="tabs" lineWidth="25vw" sticky scrollspy>
      <template slot="tab0">
        <Notice class="box" text="团队管理系统新版发布~~"></Notice>
        <Performance class="box shadow"></Performance>
        <div class="box links shadow flex ac">
          <div class="link-item item flex column center" @click="toOther('/goods')">
            <img :src="require('@/assets/goods-link.png')" alt />
            <span>商品销售排名</span>
          </div>
          <div class="link-item item flex column center" @click="toOther('/team')">
            <img :src="require('@/assets/team-link.png')" alt />
            <span>团队销售排名</span>
          </div>
          <div class="link-item item flex column center" @click="toOther('/collect')">
            <img :src="require('@/assets/refund-link.png')" alt />
            <span>销售/退货汇总</span>
          </div>
          <div class="link-item item flex column center" @click="toOther('/order')">
            <img :src="require('@/assets/order-link.png')" alt />
            <span>订单管理</span>
          </div>
        </div>
      </template>
      <template slot="tab1">
        <Hotspot class="box shadow" @click.native="toOther('/home/market', true)"></Hotspot>
        <Goods class="box shadow"></Goods>
      </template>
      <template slot="tab2">
        <Team class="box shadow"></Team>
      </template>
      <template v-if="taskVisible" slot="tab3">
        <Task class="box shadow"></Task>
      </template>
    </Tabs>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Notice from "@/components/Notice";
import Performance from "./components/Performance";
import Hotspot from "./components/Hotspot";
import Goods from "./components/Goods";
import Team from "./components/Team";
import Task from "./components/Task";
import Tabs from "@/components/Tabs";

export default {
  components: { Notice, Performance, Hotspot, Goods, Team, Task, Tabs },
  computed: {
    ...mapGetters({taskVisible: 'config/taskVisible'}),
    tabs() {
      const tabs = [
        { name: "今日业绩" },
        { name: "商品销量分析" },
        { name: "团队销量分析" }
      ];
      return this.taskVisible ? tabs.concat([{ name: "任务审核" }]) : tabs;
    }
  },
  created() {
    this.$store.dispatch("config/getConfig");
  },
  methods: {
    // 跳转
    toOther(path, replace = false) {
      if (replace) {
        if(path === '/home/market') {
          this.$toast('功能内测中')
          return
        }
        this.$router.replace(path)
        return
      }
      this.$router.push(path);
    }
  }
};
</script>

<style lang="less" scoped>
@import url("~@/design/index");
.data-page {
  padding-bottom: 72px;
  padding-bottom: calc(72px + constant(safe-area-inset-bottom));
  padding-bottom: calc(72px + env(safe-area-inset-bottom));
}

.box {
  margin: 10px 13px;
  box-sizing: border-box;
}

.shadow {
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
}

.links {
  background: @color-white;
  height: 84px;
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  border-radius: 8px;
}

.link-item {
  img {
    height: 28px;
    margin-bottom: 10px;
  }
  span {
    color: @color-text-assist;
    font-size: @font-size-12;
    line-height: 17px;
  }
}
</style>