<template>
  <Container title="团队销量分析" :time="time" :nums="nums" detailPath="/team?index=1">
    <div class="list">
      <div v-if="!list.sale_num_first" class="empty-text">暂无数据~</div>
      <div v-if="list.sale_num_first" class="list-item flex ac num">
        <CustomImg width="0.8rem" height="0.8rem" round lazy-load :src="list.sale_num_first.head_pic" />
        <div class="sale-name ellipsis">{{list.sale_num_first.nickname}}</div>
        <div class="sale-desc">昨日总销量第一</div>
      </div>
      <div v-if="list.sale_money_first" class="list-item flex ac money">
        <CustomImg width="0.8rem" height="0.8rem" round lazy-load :src="list.sale_money_first.head_pic" />
        <div class="sale-name ellipsis">{{list.sale_money_first.nickname}}</div>
        <div class="sale-desc">昨日总营业额第一</div>
      </div>
    </div>
  </Container>
</template>

<script>
import { getTeamAnalyze } from "@/api";
import Container from "./Container";

export default {
  components: { Container },
  data() {
    return {
      time: "", // 统计时间
      nums: [], // 数量
      list: {} // 榜单
    };
  },
  created() {
    this.getData();
  },
  methods: {
    // 商品销量数据
    getData() {
      getTeamAnalyze().then(res => {
        if (res.success) {
          this.time = res.data.time;
          const nums = [];
          nums.push(
            { name: "昨日新增直属(人)", num: res.data.direct },
            { name: "昨日新增团队人数", num: res.data.team_member },
            { name: "昨日新增超级VIP", num: res.data.supper_vip }
          );
          this.nums = nums;
          this.list = res.data.list;
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.list-item {
  width: 329px;
  height: 44px;
  border-radius: 5px;
  margin: 0 auto 8px;
  color: #fff;
  padding: 0 7px;
  box-sizing: border-box;
}

.num {
  background: linear-gradient(
    90deg,
    rgba(242, 198, 116, 1) 0%,
    rgba(245, 185, 103, 1) 100%
  );
}

.money {
  margin-bottom: 15px;
  background: linear-gradient(
    90deg,
    rgba(179, 208, 232, 1) 0%,
    rgba(162, 191, 218, 1) 100%
  );
}

.sale-name {
  width: 56px;
  font-size: 14px;
  font-weight: 500;
  margin: 0 10px;
}

.sale-desc {
  font-size: 12px;
}
</style>