<template>
  <div class="container">
    <div class="container-title flex ac js">
      <span>{{title}}</span>
      <span v-show="time">统计时间：{{time}}</span>
    </div>
    <div v-if='nums.length' class="container-nums flex ac">
      <div v-for="(item, index) in nums" :key="index" class="num-item">
        <div>{{item.name}}</div>
        <div>{{item.num}}</div>
      </div>
    </div>
    <slot></slot>
    <div v-if='detailPath' @click="toDetail" class="container-btn flex ac js">
      <span>查看详情</span>
      <img :src="require('@/assets/right.png')" alt />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "标题"
    },
    time: {
      type: String,
      default: ""
    },
    detailPath: {
      type: String,
      default: ""
    },
    nums: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    toDetail() {
      this.$router.push(this.detailPath)
    }
  },
};
</script>

<style lang="less" scoped>
@import url("~@/design/index");
.container {
  border-radius: 8px;
  background: @color-white;
  overflow: hidden;
}

.container-title {
  padding: 10px;
  span:first-child {
    color: @color-text-main;
    font-size: @font-size-16;
    line-height: 23px;
  }
  span:last-child {
    color: @color-text-sub;
    font-size: @font-size-12;
  }
}

.container-nums {
  margin: 10px 0 20px;
}

.num-item {
  width: 33.33%;
  padding-left: 10px;
  position: relative;
  &::after {
    content: "";
    display: inline-block;
    width: 1px;
    height: 24px;
    background: rgba(216, 216, 216, 1);
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
  &:last-child:after {
    display: none;
  }
  div:first-child {
    color: @color-text-sub;
    font-size: @font-size-12;
    line-height: 19px;
  }
  div:last-child {
    color: #252525;
    font-size: @font-size-16;
    line-height: 23px;
  }
}

.container-btn{
  padding: 15px 10px;
  color: #252525;
  font-size: @font-size-14;
  border-top: 1px solid @color-line;
  img{
    width: 6px;;
  }
}
</style>