<template>
  <Container title='任务审核'>
    <div class="task flex ac js" @click="toPath('/task')">
      <img :src="require('@/assets/task.png')" alt="">
      <span class="item">有 {{count}} 团长提交了社群任务，请审核</span>
      <img :src="require('@/assets/right.png')" alt="">
    </div>
  </Container>
</template>

<script>
import { getTaskCount } from '@/api'
import Container from './Container'

export default {
  components: { Container },
  data() {
    return {
      count: 0,
    }
  },
  created() {
    this.getData()
  },
  methods: {
    // 数量
    getData() {
      getTaskCount().then(res => {
        if (res.success) {
          this.count = res.data.count
        }
      })
    },
    // 跳转
    toPath(path) {
      this.$router.push(path)
    }
  },
}
</script>

<style lang="less" scoped>
.task{
  padding: 0 10px 15px;
  color: #282828;
  font-size: 16px;
  img:first-child{
    width: 43px;
    margin-right: 7px;
  }
  img:last-child{
    width: 6px;
  }
}
</style>