<template>
  <div class="performance flex column">
    <div class="performance-top item">
      <div class="performance-title flex ac js">
        <div>今日业绩</div>
        <div class="item">截止时间 {{time}}</div>
        <div class="flex ac jc" @click="getData">
          <img :src="require('@/assets/reload.png')" alt />刷新
        </div>
      </div>
      <div class="performance-goods flex ac" @click="toOther('/goods')">
        <div class="performance-item">
          <div>商品销售数量</div>
          <div>
            {{sale_num}}
            <span>件</span>
          </div>
        </div>
        <div class="line"></div>
        <div class="performance-item">
          <div>退款商品数</div>
          <div>{{refund_num}}</div>
        </div>
      </div>
    </div>
    <div class="performance-bottom flex ac"  @click="toOther('/pepole')">
      <div class="performance-item">
        <div>新增直属成员</div>
        <div>
          {{direct}}
          <span>人</span>
        </div>
      </div>
      <div class="line"></div>
      <div class="performance-item">
        <div>新增团队成员</div>
        <div>
          {{team_member}}
          <span>人</span>
        </div>
      </div>
      <div class="line"></div>
      <div class="performance-item">
        <div>新增超级VIP</div>
        <div>
          {{supper_vip}}
          <span>人</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getTodayPerformance } from "@/api";

export default {
  data() {
    return {
       sale_num: 0, // 商品销售数量
       refund_num: 0, // 退款商品数量
       direct: 0, // 新增直属成员
       team_member: 0, // 新增团队成员
       supper_vip: 0, // 新增超级VIP
       time: "" // 截止时间
    };
  },
  created() {
    this.getData()
  },
  methods: {
    // 业绩数据
    getData() {
      getTodayPerformance().then(res => {
        if(res.success) {
          const { sale_num, refund_num, direct, team_member, supper_vip, time} = res.data
          this.sale_num = sale_num
          this.refund_num = refund_num
          this.direct = direct
          this.team_member = team_member
          this.supper_vip = supper_vip
          this.time = time
        }
      });
    },
    // 跳转商品
    toOther(path) {
      this.$router.push(path)
    }
  }
};
</script>

<style lang="less" scoped>
.performance {
  width: 349px;
  height: 200px;
  color: #fff;
  background: linear-gradient(
    180deg,
    rgba(216, 41, 47, 1) 0%,
    rgba(227, 80, 50, 1) 100%
  );
  box-shadow: 0px 0px 4px 0px rgba(193, 193, 193, 0.5);
  border-radius: 5px;
}

.performance-top {
  border-radius: 5px 5px 0 0;
}

.performance-bottom {
  border-radius: 0 0 5px 5px;
  height: 80px;
  box-sizing: border-box;
  background: linear-gradient(
    360deg,
    rgba(226, 77, 50, 1) 0%,
    rgba(215, 51, 42, 1) 100%
  );
}

.performance-title {
  margin: 10px 0 20px 10px;
  div:first-child {
    font-size: 16px;
    line-height: 23px;
    margin-right: 10px;
  }
  div:nth-child(2) {
    color: #ffa39f;
    font-size: 10px;
  }
  div:last-child {
    width: 60px;
    height: 23px;
    background: rgba(178, 37, 35, 0.4);
    border-radius: 12px 0px 0px 12px;
    font-size: 14px;
    align-self: flex-end;
    img {
      width: 13px;
      margin-right: 4px;
    }
  }
}

.performance-item {
  padding: 0 17px 0 10px;
  div:first-child {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 5px;
  }
  div:last-child {
    font-size: 18px;
    font-weight: 500;
    span {
      font-size: 13px;
    }
  }
}

.line {
  width: 1px;
  height: 30px;
  margin-right: 10px;
  background: rgba(246, 91, 77, 1);
}
</style>